.wq-Form .wq-Control {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    font-size: $font-size;
    line-height: $line-height;
    color: $base-color;
    @if $table-layout {
        &:not(.wq-for-action) {
            border-bottom: $item-border-width solid $border-color;
            &:first-child {
                border-top: $item-border-width solid $border-color;
            }
            & > .wq--header {
                background: $alt-bgcolor;
            }
        }
    }
    & > .wq--header {
        flex: 0 0 auto;
        width: $control-header-width;
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        padding: cpd(-1 * $item-border-width) 0;
        box-sizing: border-box;
        @if $table-layout {
            padding: (cpd(-1 * $item-border-width) + $half-big-padding) $small-padding;
            align-self: stretch;
        }
        & > .wq--label {
            flex: 0 1 auto;
            font-size: $small-font-size;
            line-height: $font-size * $line-height;
            font-weight: bold;
            @if $flip-required {
                order: 2;  // hard-code-checked
            }
        }
        & > .wq--required {
            flex: 0 0 auto;
            font-size: $small-font-size;
            line-height: $font-size * $line-height;
            font-weight: normal;
            @if $fill-required {
                background: $accent-color;
                color: $base-bgcolor;
                border-radius: $control-radius;
                border: none;
                padding: 0 ($nano-font-size * 0.5);  // hard-code-checked
                font-size: $nano-font-size;
            } @else {
                color: $accent-color;
            }
            &.wq-is-optional {
                @if $show-optional {
                    @if $fill-required {
                        @if $table-layout {
                            background: $base-bgcolor;
                        } @else {
                            background: $alt-bgcolor;
                        }
                        color: $base-color;
                    }
                    color: rgba($base-color, $weak-alpha);
                } @else {
                    display: none;
                }
            }
        }
        &.wq-content-empty > .wq--required {
            display: none;
        }
        & > *:not(:last-child) {
            @if $flip-required {
                margin-left: $small-padding;
            } @else {
                margin-right: $small-padding;
            }
        }
    }
    & > .wq--body {
        flex: 1 1 auto;
        position: relative;
        @if $table-layout {
            padding: $half-big-padding $padding;
        }
        & > *:not(:first-child) {
            margin-top: $small-padding;
        }
        & .wq--note {
            display: block;
            font-size: $small-font-size;
            opacity: $weak-alpha;
            margin-top: $small-padding;
            margin-left: $small-padding;
        }
        & > .wq--message {
            @extend %Message;
            position: absolute;
            @if $table-layout {
                top: $half-big-padding - $small-font-size - $small-padding;
                left: $padding;
            } @else {
                left: $small-padding;
                top: - $small-font-size - $small-padding;
            }
        }
    }
    &.wq-is-run.wq-for-removed {
        display: none;  // suppress lifecycle transition
    }
}
@media screen and (max-width:$control-break-width) {
    .wq-Form .wq-Control {
        flex-direction: column;
        & > .wq--header {
            width: 100%;
